import React from 'react';
import { Breadcrumb } from 'antd';
import { Link, useLocation } from 'react-router-dom';

interface Props {
  language: 'zh-CN' | 'zh-TW' | 'en';
}

const BreadcrumbDisplay: React.FC<Props> = ({ language }) => {
  const location = useLocation();
  const pathSnippets = location.pathname.split('/').filter(i => i);

  const breadcrumbNameMap: Record<string, Record<string, string>> = {
    '/': { 'zh-CN': '', 'zh-TW': '', 'en': 'Home' },
    '/about-us': { 'zh-CN': '', 'zh-TW': '', 'en': 'About Us' },
    '/biosheng-healthcare': { 'zh-CN': '', 'zh-TW': '', 'en': 'Biosheng Healthcare' },
    '/products': { 'zh-CN': '', 'zh-TW': '', 'en': 'Products' },
    '/products/functional': { 'zh-CN': '', 'zh-TW': '', 'en': 'Functional Products' },
    '/products/daily-care': { 'zh-CN': '', 'zh-TW': '', 'en': 'Daily Care' },
    '/customer-events': { 'zh-CN': '', 'zh-TW': '', 'en': 'External Collaborations' },
    '/core-concept': { 'zh-CN': '', 'zh-TW': '', 'en': 'Core Concept' },
    '/business-model': { 'zh-CN': '', 'zh-TW': '', 'en': 'Business Model' },
    '/endoangel-full': { 'zh-CN': 'EndoAngel ', 'zh-TW': 'EndoAngel ', 'en': 'EndoAngel' },
    '/contact': { 'zh-CN': '', 'zh-TW': '', 'en': 'Contact' },
    '/customer-care': { 'zh-CN': '', 'zh-TW': '', 'en': 'Customer Care' },
  };

  const extraBreadcrumbItems = pathSnippets.map((_, index) => {
    const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
    const isLast = index === pathSnippets.length - 1;
    const title = breadcrumbNameMap[url] ? breadcrumbNameMap[url][language] : undefined;

    return title ? (
      <Breadcrumb.Item key={url}>
        {isLast ? (
          title
        ) : (
          <Link to={url}>{title}</Link>
        )}
      </Breadcrumb.Item>
    ) : null;
  }).filter(Boolean); // Removes null values if any

  const homeBreadcrumb = (
    <Breadcrumb.Item key="home">
      <Link to="/">{breadcrumbNameMap['/'][language]}</Link>
    </Breadcrumb.Item>
  );

  const breadcrumbItems = [homeBreadcrumb, ...extraBreadcrumbItems];

  return (
    <Breadcrumb style={{ marginBottom: '16px' }}>
      {breadcrumbItems}
    </Breadcrumb>
  );
};

export default BreadcrumbDisplay;